import React, { Component } from 'react'
import Ding from './Ding.wav'

var ding = new Audio(Ding)

export default class Clock extends Component {
    state = {
        minuteTimer: 25,
        secondTimer:  '00',
        sessionMinute: 25,
        breakMinute: 5,
        toggle: 'start',
        stage: 'Session',
        running: false,
        counting: false
    }

    add = (e) => {
        if (this.state.toggle === 'start' && e.target.id === "arrowLeftBreak" && this.state.breakMinute !== 60) {
            this.setState({
                breakMinute: this.state.breakMinute + 1
            })
            if (this.state.stage==="Break") {
                if (this.state.breakMinute < 9) {
                    this.setState({
                        minuteTimer: `0${this.state.breakMinute + 1}`,
                        secondTimer: '00'
                    })
                }
                else {
                    this.setState({
                        minuteTimer: this.state.breakMinute + 1,
                        secondTimer: '00'
                    })
                }
            }
        }
        else if (this.state.toggle === 'start' && e.target.id === "arrowLeftSession" && this.state.sessionMinute !== 60) {
            this.setState({
                sessionMinute: this.state.sessionMinute + 1
            })
            if (this.state.stage === "Session") {
                if (this.state.sessionMinute < 9) {
                    this.setState({
                        minuteTimer: `0${this.state.sessionMinute + 1}`,
                        secondTimer: '00'
                    })
                }
                else {
                    this.setState({
                        minuteTimer: this.state.sessionMinute + 1,
                        secondTimer: '00'
                    })
                }
            }
        }
    }

    subtract = (e) => {
        if (this.state.toggle === 'start' && e.target.id === "arrowRightBreak" && this.state.breakMinute > 1) {
            this.setState({
                breakMinute: this.state.breakMinute - 1
            })
            if (this.state.stage==="Break") {
                if (this.state.breakMinute < 11) {
                    this.setState({
                        minuteTimer: `0${this.state.breakMinute - 1}`,
                        secondTimer: '00'
                    })
                }
                else {
                    this.setState({
                        minuteTimer: this.state.breakMinute - 1,
                        secondTimer: '00'
                    })
                }
            }
        }
        else if (this.state.toggle === 'start' && e.target.id === "arrowRightSession" && this.state.sessionMinute > 1) {
            this.setState({
                sessionMinute: this.state.sessionMinute - 1
            })
            if (this.state.stage === 'Session') {
                if (this.state.sessionMinute < 11 && this.state.minuteTimer < 11) {
                    this.setState({
                        minuteTimer: `0${this.state.sessionMinute - 1}`,
                        secondTimer: '00'
                    })
                }
                else {
                    this.setState({
                        minuteTimer: this.state.sessionMinute - 1,
                        secondTimer: '00'
                    })
                }
            }
        }
    }

    toggle = (e) => {
        if (this.state.running === false && this.state.toggle === 'start' && this.state.counting === false) {
            if (this.state.minuteTimer <= 10) {
                this.setState({
                    toggle: 'stop',
                    secondTimer: 59,
                    minuteTimer: `0${this.state.sessionMinute - 1}`,
                    running: true,
                    counting: true
                })
            }
            else {
                this.setState({
                    toggle: 'stop',
                    secondTimer: 59,
                    minuteTimer: this.state.sessionMinute - 1,
                    running: true,
                    counting: true
                })
            }
            this.count();
        }
        else if (this.state.toggle === 'start') {
            this.setState({
                toggle: 'stop',
                running: true
            })
        }
        else {
            this.setState({
                toggle: 'start',
                running: false
            })
        }
    }

    reset = (e) => {
        this.setState({
            minuteTimer: 25,
            secondTimer: '00',
            sessionMinute: 25,
            breakMinute: 5,
            toggle: 'start',
            stage: 'Session',
            running: false
        })
    }

    count = () => {
        setInterval(()=>{
            if (this.state.running === true) {    
                if (this.state.secondTimer !== '00') {
                    if (this.state.secondTimer <= 10) {
                        this.setState({
                            secondTimer: `0${this.state.secondTimer - 1}`
                        })
                    }
                    else {
                        this.setState({
                            secondTimer: this.state.secondTimer - 1
                        })
                    }
                }
                else if (this.state.secondTimer === '00' && this.state.minuteTimer !== '00') {
                    if (this.state.minuteTimer <= 10) {
                        this.setState({
                            minuteTimer: `0${parseInt(this.state.minuteTimer) - 1}`
                        })
                    }
                    else {
                        this.setState({
                            minuteTimer: this.state.minuteTimer - 1
                        })
                    }
                    setTimeout(()=>{
                        this.setState({
                            secondTimer: 59
                        })
                    })
                }
                else if (this.state.minuteTimer === '00' && this.state.secondTimer === '00') {
                    this.setState({
                        shifting: true
                    })
                    document.getElementById('timerText').style.color = 'red'
                    ding.play();
                    ding.currentTime=0;
                    setTimeout(()=>{
                        document.getElementById('timerText').style.color = 'white'
                        if (this.state.stage === 'Session' && this.state.shifting !== false) {
                            if (this.state.breakMinute < 10) {
                                this.setState({
                                    minuteTimer: `0${this.state.breakMinute}`,
                                    stage: 'Break',
                                    shifting: false
                                })
                            }
                            else {
                                this.setState({
                                    minuteTimer:this.state.breakMinute,
                                    stage: 'Break',
                                    shifting: false
                                })
                            }
                        }
                        else if (this.state.stage === 'Break' && this.state.shifting !== false) {
                            if (this.state.sessionMinute < 10) {
                                this.setState({
                                    minuteTimer: `0${this.state.sessionMinute}`,
                                    stage: 'Session',
                                    shifting: false
                                })
                            }
                            else {
                                this.setState({
                                    minuteTimer:this.state.sessionMinute,
                                    stage: 'Session',
                                    shifting: false
                                })
                            }
                        }
                    },2000)
                }
            }
        },1000)
    }

    handleTouchStart = (e) => {
        e.target.style.color = 'black'
    }
    handleTouchEnd = (e) => {
        e.target.style.color = 'white'
    }

    render() {
    return (
      <div style={clock}>
        <div style={titles}>
            <h4 style={breakDialsText}>Break<br />Length</h4>
            <h4 style={sessionDialsText}>Session<br />Length</h4>
        </div>
        <div style={numbers}>
            <h4 onTouchStart={this.handleTouchStart} onTouchEnd={this.handleTouchEnd} onClick={this.add} id="arrowLeftBreak" name="break">↑</h4>
            <h1 style={numberText}>{this.state.breakMinute}</h1>
            <h1 onTouchStart={this.handleTouchStart} onTouchEnd={this.handleTouchEnd} onClick={this.subtract} id="arrowRightBreak" name="break">↓</h1>
            <h1 onTouchStart={this.handleTouchStart} onTouchEnd={this.handleTouchEnd} onClick={this.add} id="arrowLeftSession" name="session">↑</h1>
            <h1 style={numberText}>{this.state.sessionMinute}</h1>
            <h1 onTouchStart={this.handleTouchStart} onTouchEnd={this.handleTouchEnd} onClick={this.subtract} id="arrowRightSession" name="session">↓</h1>

        </div>
        <div style={timer}>
            <h3 style={stage}>{this.state.stage}</h3>
            <h4 id="timerText" style={timerText}>{this.state.minuteTimer}:{this.state.secondTimer}</h4>
        </div>
        <div style={toggle}>
            <h3 onTouchStart={this.handleTouchStart} onTouchEnd={this.handleTouchEnd} onClick={this.toggle} id="toggle">{this.state.toggle}</h3>
            <h3 onTouchStart={this.handleTouchStart} onTouchEnd={this.handleTouchEnd} onClick={this.reset} id="reset">reset</h3>
        </div>
      </div>
    )
  }
}

const clock = {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column'
}

const titles = {
    display: 'flex',
    flexDirection: 'row',
    fontSize: '15px',
    marginTop: '-20px'
}

const breakDialsText = {
    marginTop: '20px',
    textAlign: 'center'
}

const sessionDialsText = {
    marginTop: '20px',
    marginLeft: '55px',
    textAlign: 'center'
}

const numbers = {
    display: 'flex',
    marginTop: '10px',
    marginBottom: '-40px',
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'row',
}

const numberText = {
    marginTop: '15px'
}

const timer = {
    display: 'flex',
    textAlign: 'center',
    flexDirection: 'column',
    border: '5px white solid',
    borderRadius: '10px',
    padding: '10px',
    marginTop: '60px',
    lineHeight: '1'
}

const stage = {
    padding: '5px'
}

const timerText = {
    fontSize: '50px'
}

const toggle = {
    display: 'flex',
    marginTop: '10px',
    flexDirection: 'row'
}
