import React, { Component } from 'react';
import './App.css';
import Clock from './components/Clock';
import { BrowserRouter as Router, Route } from 'react-router-dom';

class App extends Component {
  render() {
    return (
      <Router>
          <div style={main}>
            <h1 style={headingText}>Promodoro Clock</h1>
            <Route path="/" render={props=>(<Clock />)}/>
        </div>
      </Router>
    );
  }
}

const main = {
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  justifyContent: 'center',
  height: '80vh'
}

const headingText = {
  fontSize: '25px',
  marginBottom: '20px',
  textAlign: 'center'
}

export default App;